
import XImg from 'vux/src/components/x-img/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import headerBanner from '../../components/header-banner';
import bottomAboutUs from '../../components/bottomAboutUs';
export default {
name: "applyJoin",
data() {
return {
bannerCont:{bannerUrl:"http://www.itwaibaow.com/uploadfile/2016/0518/20160518085709729.jpg",bannerTitle:"联系我们"},
joinUsHeadImg:"../../../static/images/home/joinUsHeadImg.jpg",

ftclcCont1:[{
textCont:"企业管理咨询"
},{
textCont:"信息技术服务"
},{
textCont:"应用维护服务"
}],
ftclcCont2:[{
textCont:"管理咨询服务"
},{
textCont:"企业it系统规划"
},{
textCont:"it整体外包"
}],
ftclcCont:[{
textCont:"HR电话:15221175771(刘)"
},{
textCont:"公司电话:63300002"
},{
textCont:"hr@keepthinking.com.cn"
}],

submitData:{
name:"",
telephone:"",
email:"",
context:""
}
}
},
methods: {
formSubmit(){
if (this.submitData.name && this.submitData.telephone && this.submitData.context) {
if (!/^[0-9-]+$/.test(this.submitData.telephone)) {
this.$vux.toast.show({
text: "请填写正确的手机号码！",
type: "text",
})
return false;
}else if(this.submitData.email && !/^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/.test(this.submitData.email)){
this.$vux.toast.show({
text: "请填写正确的邮箱！",
type: "text",
})
return false;
}


















}else{
this.$vux.toast.show({
text: "请填写正确的联系信息！",
type: "text",
})
}
},
},
components: {
headerBanner,
XImg,
Flexbox,
FlexboxItem,
Actionsheet,
bottomAboutUs
}

}



